













import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class Decoration extends Vue {
  @Prop({
    type: Number,
    default: 230,
  })
  readonly size!: number;

  @Prop({
    type: Number,
    default: 2,
  })
  readonly width!: number;

  @Prop({
    type: String,
    default: 'white',
  })
  readonly color!: string;
}
